.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}


.fade-alert {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  border-radius: 5px;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.fade-alert.show {
  opacity: 1;
}

/* toggle switch css start */

input[type="checkbox"]:checked+label {
  background: green;
}

input[type="checkbox"]:checked+label:after {
  left: calc(100% - 5px);
  transform: translateX(-100%);
}

.form-check {
  margin-left: auto;
}

.form-check input[type="checkbox"] {
  height: 0;
  width: 0;
  visibility: hidden;
}

.form-check .form-check-label:after {
  content: "";
  position: absolute;
  top: 2px;
  left: 5px;
  width: 15px;
  height: 15px;
  background: black;
  border-radius: 90px;
  transition: 0.3s;
}
.form-check .form-check-label {
  cursor: pointer;
  text-indent: -9999px;
  width: 50px;
  height: 20px;
  background: #a4a5b3;
  display: inline-block;
  margin-left: 10px;
  border-radius: 100px;
  position: relative;
}
input:checked + label {
  background: #000;
}
input:checked + label:after {
  left: calc(100% - 5px);
  transform: translateX(-100%);
}
.form-check input[type=checkbox] {
  height: 0;
  width: 0;
  visibility: hidden;
}

.form-check h4 {
  margin-bottom: 0;
  display: inline-block;
  font-size: 18px;
  
} 

/* toggle switch css end */

.placeholder-light::placeholder {
  color: white;
}